import './styles/main.scss';
import ActivitiesManager from './modules/activities.js';
import * as storage from './modules/dataStorage.js';

const itemContainer = document.querySelector('#itemsContainer');
const addInput = document.querySelector('#addTask');
const refresh = document.querySelector('#refresh');
const buttonAdd = document.querySelector('#enter');
const buttonDelete = document.querySelector('#buttonDelete');

const activitiesManager = new ActivitiesManager(itemContainer, storage);
activitiesManager.refresh();
addInput.addEventListener('keypress', (event) => {
  if (event.key === 'Enter') {
    // code for enter
    const value = addInput.value.trim();
    addInput.value = '';
    if (value !== '') activitiesManager.addTask(value);
  }
});

buttonAdd.addEventListener('click', () => {
  const value = addInput.value.trim();
  addInput.value = '';
  if (value !== '') activitiesManager.addTask(value);
});

refresh.addEventListener('click', () => {
  refresh.classList.add('fa-spin');
  itemContainer.innerHTML = '';
  setTimeout(() => {
    refresh.classList.remove('fa-spin');
    activitiesManager.refresh();
  }, 2000);
});

buttonDelete.addEventListener('click', () => {
  activitiesManager.deleteAllComplete();
});

function printInformation(info) {
  //console.log(info);
}

let elementDraged = null;

itemContainer.addEventListener('dragstart', (event) => {
  console.log('drag started on: ', event);
  elementDraged = event.target;
  printInformation(event.clientX);
});

itemContainer.addEventListener('dragover', (event) => {
  // prevent defult do allow drop
  console.log('drag iver', event);
  event.preventDefault();
});

itemContainer.addEventListener('drop', (event) => {
  // prevent defult actions (open as link for some elements)
});

itemContainer.addEventListener('dragend', (event) => {
  console.log('drag end on: ', event);
  if (event.dataTransfer !== null) {
    console.log(event.dataTransfer);
  }
  printInformation(event.clientX);
}, false);

window.addEventListener('mousemove', printInformation);
